@import url(https://fonts.googleapis.com/css2?family=STIX+Two+Text:wght@400;600&display=swap);
html, body {
  font-size: 62.5%;
  margin: 0;
  scroll-behavior: smooth;
  font-family: 'STIX Two Text', serif;
}

h1 {
  font-size: 6rem;
  font-family: 'STIX Two Text', serif;
}

h2 {
  font-size: 4rem;
  margin: 0;
}

h3 {
  font-size: 3rem;
  margin: 0;
}

h4 {
  font-size: 2.5rem;
}

p {
  font-size: 1.8rem;
  margin: 0;
  line-height: 130%;
}

a {
  font-size: 2rem;
}

hr {
  width: 80%;
}

@media screen and (max-width: 360px) {
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 2.5rem;
  }

  h3 {
    font-size: 2.2rem;
  }

  p {
    font-size: 1.6rem;
  }
}

#home-description-container{box-sizing:border-box;padding:10rem 1rem;height:100vh;background-image:url(/portfolio/static/media/background.e88cac6e.png);display:flex;justify-content:center;align-items:center}#home-aboutme-container{box-sizing:border-box;padding:10rem 13rem;width:100%}#home-project-container{box-sizing:border-box;padding:10rem 13rem;background-color:#8d99ae;display:flex;flex-direction:column;align-items:center}#footer-container{box-sizing:border-box;padding:10rem 13rem;height:30vh;display:flex;align-items:center}@media only screen and (max-width: 360px){#home-page-container{box-sizing:border-box;margin:0;padding:3px}#home-aboutme-container{flex-direction:column;padding:3rem 1rem}#home-project-container{padding:3rem 1rem;margin-bottom:2rem}#footer-container{padding:3rem 1rem}}
#heading-container{display:flex;flex-direction:column;align-items:center}#heading-text{color:#edf2f4;text-align:center}#heading-name{color:#ef233c}
#aboutMe-content-container{margin:0;display:flex;justify-content:space-around}#aboutMe-heading{margin:1.5rem 0}#portfolioPic{height:50rem;border-radius:3rem;margin:2rem 0}#content-container{display:flex;flex-direction:column;align-items:center}#aboutMe-content{line-height:150%;font-size:1.8rem;white-space:pre-wrap}@media only screen and (max-width: 360px){#aboutMe-content-container{box-sizing:border-box;margin-bottom:3rem}#portfolioPic{height:25rem}#aboutMe-content{font-size:2rem}}
#technologies-lists-container{box-sizing:border-box;padding:2rem;text-align:center}#tech-heading{margin:2rem;margin-bottom:1rem}#hr-line{margin-bottom:2rem}#tech-text{font-size:2rem;margin-top:1rem}@media only screen and (max-width: 360px){#technologies-lists-container{padding:2rem;margin-bottom:2rem}}
#text-box{box-sizing:border-box;height:100%;width:100%;padding:1rem 2rem;background-color:#edf2f4;display:flex;flex-direction:column;align-items:center}@media screen and (max-width: 480px){#text-box{border-radius:4rem;height:auto;width:100%}}
#projects-heading{color:#fff;margin-bottom:2rem}#projects-container{display:flex;justify-content:space-between}#project{box-sizing:border-box;max-width:60rem;padding:2rem;display:flex;flex-direction:column;align-items:center}#image-container{height:25rem;margin-bottom:3rem}#project-image{height:100%;border:4px solid #edf2f4;transition:-webkit-transform .5s ease;transition:transform .5s ease;transition:transform .5s ease, -webkit-transform .5s ease}#project-image:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}#project-heading{display:inline;margin-bottom:1.5rem}#project-content{font-size:2rem}@media only screen and (max-width: 360px){#projects-container{flex-direction:column}#project{max-width:100%;padding:1rem}#image-container{height:100%;margin-bottom:2rem}#project-image{height:15rem}}
#link-component-container{width:100%;display:flex;justify-content:space-around}@media only screen and (max-width: 360px){#link-component-container{height:100%;flex-direction:column}}
#links-container{display:flex;flex-direction:column;align-items:center}#link-text{color:#284b63;margin-bottom:.3em;text-decoration:none}#link-text:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}#link-text:visited{color:#ef233c}@media only screen and (max-width: 360px){#links-container{margin-bottom:2rem}}
